#root{
    display: -ms-flexbox;
    display: flex;
    height: 100%;
}

.container{
    margin: auto;
    -ms-flex-pack:center;
        justify-content:center;
}
html, body{
    height: 100%;
    background: #fafafa;
}
body {
    /*background: #2196f3;*/
    height: 100%;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-title {
    font-size: 1.5em;
}

.App-intro {
    font-size: large;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.Junk {
    color: #AAA !important;
}

.Basic {
    color: #000 !important;
}

.Fine {
    color: #62A4DA !important;
}

.Masterwork {
    color: #1a9306 !important;
}

.Rare {
    color: #fcd00b !important;
}

.Exotic {
    color: #ffa405 !important;
}

.Ascended {
    color: #fb3e8d !important;
}

.Legendary {
    color: #4C139D !important;
}



